
.quiz-button {
  margin-top: 25px !important;
  margin-bottom: 25px !important;
}













































.question-text {
    text-align: center;
}
.question-index {
    text-align: center;
    margin-bottom: 30px !important;
    margin-top: 10px;
}
.opt-and-image-container {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
}
.options-container {
    flex-grow: 1;
    width: 50%;
}
.image-container {
    flex-grow: 1;
    width: 50%;
    padding-left: 25px;
}
.image-container img {
    height: 350px;
    text-align: center;
    max-height: 350px;
}
.option {
    display: block;
    width: 100%;
    margin-bottom: 10px;
}
.correct {
    background: green !important;
}
.wrong {
    background: red !important;
}

#completed {
  text-align: center;
  margin-bottom: 25px;
}
.chart-wrapper {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  margin-top: 25px;
  margin-bottom: 25px;
}
.chart-container {
  background: #f8f8f8;
  padding: 25px;
  width: calc(50% - 5px);
}
.chart-container h4 {
  padding-top: 0;
  padding-bottom: 15px;
  margin-bottom: 0;
  margin-top: 0;
}
#average-chart {
  margin: 0 auto;
}
.result-buttons {
  float: left;
}
